import React from 'react'
import { Link } from 'gatsby'

import Layout from '../components/layout'

const IndexPage = () => (
  <Layout>
    <h2>The world needs you</h2>
    <p>
    	<b>We request of you one hard decision. Please cooperate with the other heads of state in the drastic task of converting the world's energy infrastructure to sustainable sources. </b> 
    </p> 
    <p>	
    	We understand that your political interest is at risk but not cooperating with the collective will post serious damage to the world. We need you to become part of the catalyst as you
    	govern one of the most influential positions in this world. 
  	</p> 
    <p>
    	You are a key component to the world's survival. We only have less than 12 years remaining (until 2030) based on Intergovermental panel on climate change report to take massive action. Time is the most scarce resource that we will have for this daunting task. The billions of dollars that America will lose from coal plants will be essential to the preservation of trillions of lives in all forms in the next 100 years.
    	We believe in the ability of humans to choose the right thing, given the right circumstances.. We hope that you share your power, intellect and resources to us.
    </p> 
    <p>
    	I'm not part of any political party or any organization but represent the inhabitants of this beautiful world - namely all the trees and insects, fishes and corals, animals and human.
    </p> 
    <p>
    	 <b>The world needs you.</b>
    </p>

    <div style={{ maxWidth: '300px', marginBottom: '1.45rem' }}>
    </div>
    <Link to="/citizensoftheworld/index.html">#dearcitizensoftheworld</Link>
  </Layout>
)

export default IndexPage
